<template>
  <div class="envelope animated bounceInLeft_">
    <div class="triangle-down"></div>
    <img class="heart" src="/static/download.png" @click="handleClick">
    <div class="triangle-up"></div>
    <div class="text">
      <font style="vertical-align: inherit;">
        <font style="vertical-align: inherit;">你收到一封信，点击查收！</font>
      </font>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Envelope',
  data () {
    return {
      // is
    }
  },
  methods : {
    handleClick () {
      this.$emit('heart-click');
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.envelope {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 360px;
  height: 250px;
}
.triangle-down{
    border-left: 180px solid transparent;
    border-right: 180px solid transparent;
    border-top: 125px solid pink;
}
.triangle-up{
    border-left: 180px solid transparent;
    border-right: 180px solid transparent;
    border-bottom: 125px solid pink;
}
.heart{
  left: 156px;
  top: 99px;
}
.text{
  left: 44px;
  top: -100px;
}
</style>



