<template>
  <div >
    <Envelope v-if="isShowEmvelope" @heart-click="handleHartClick"/>
    <Main v-if="iShowMain"/>
  </div>
</template>

<script>
import Envelope from './Envelope'
import Main from './Main'
export default {
  components: {
    Envelope,
    Main
  },
  data () {
    return {
      isShowEmvelope: false,
      iShowMain: false,
    }
  },
  mounted () {
    this.isShowEmvelope = true;
  },
  methods: {
    handleHartClick () {
      this.isShowEmvelope = false;
      this.iShowMain = true;
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



