<template>
<div class="App animated bounceInLeft_">
  <div class="date">
    <p>
      <font style="vertical-align: inherit;">
        <font style="vertical-align: inherit;">我们已经一起走过了：</font>
      </font>
      <span class="date-text">
        <font style="vertical-align: inherit;">
          <font style="vertical-align: inherit;">{{day}}</font>
        </font>
      </span>
      <font style="vertical-align: inherit;">
        <font style="vertical-align: inherit;">天</font>
      </font>
      <span class="date-text">
        <font style="vertical-align: inherit;">
          <font style="vertical-align: inherit;">{{hour}}</font>
        </font>
      </span>
      <font style="vertical-align: inherit;">
        <font style="vertical-align: inherit;">小时</font>
      </font>
      <span class="date-text">
        <font style="vertical-align: inherit;">
          <font style="vertical-align: inherit;">{{min}}</font>
        </font>
      </span>
      <font style="vertical-align: inherit;">
        <font style="vertical-align: inherit;">分</font>
      </font>
      <span class="date-text">
        <font style="vertical-align: inherit;">
          <font style="vertical-align: inherit;">{{sec}}</font>
        </font>
      </span>
      <font style="vertical-align: inherit;">
        <font style="vertical-align: inherit;">秒</font>
      </font>
    </p>
  </div>
  
  <div id="autotype">
    <h1 style="font-weight: 900;text-align: left;">哈喽！wuli小艳艳！</h1>
    <p>在煽情开始之前，先放首歌当背景音乐吧！Music!</p>
    <p>艳艳，现在是凌晨12点整，从现在开始哥哥跟你保证，答应你的不抽烟，一定会做到，不忍见你伤心，你的泪痕是对我最大的惩罚。</p>
    <p>本来是想着满一个月的时候给你看的，但是我发现自己无时无刻不在想你，所以没办法啦，只好把自己的对你的爱，用这蹩脚的方式表达出来啦</p>
    <p>最后祝wuli小艳艳天天开心！</p>
    <div style="text-align:right">
      <p>爱你的哥哥</p>
      <p>2019年11月26日</p>
    </div>
  </div>
  <audio ref="audio" id="audio" src="http://rychouuuu.coding.me/two_year/static/media/gbqq.1cb9e662.mp3"></audio>
</div>
</template>

<script>
export default {
  name: 'Envelope',
  data () {
    return {
      startTime: new Date('2019/11/03 21:34:00').getTime(),
      dayTmp: 24 * 3600 * 1000,
      hourTmp: 3600 * 1000,
      minTmp: 60 * 1000,
      secTmp: 1000,

      day: null,
      hour: null,
      min: null,
      sec: null,
    }
  },
  methods: {
    play () {
      setTimeout(() => {
        this.$refs['audio'].play();
      }, 8600);
    },
    pause () {
      this.$refs['audio'].pause();
    },
    startRunTimeCalc () {
      setInterval(() => {  
        let time = new Date().getTime() - this.startTime;
        const day = Math.floor(time / this.dayTmp);
        time = time - day * this.dayTmp;
        const hour = Math.floor(time / this.hourTmp);
        time = time - hour * this.hourTmp;
        const min = Math.floor(time / this.minTmp);
        time = time - min * this.minTmp;
        const sec = Math.floor(time / this.secTmp);
         this.day =day;
         this.hour = hour;
         this.min =  min;
         this.sec = sec; 
      }, 1000);
    },
    startWrite () {
      window.$.fn.autotype = function() {
            var _this=$(this);
            var str=_this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str=str.replace(/(\s){2,}/g,"$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args=arguments;
                var current = str.slice(index, index+1);
                // html标签完整输出,如：<p>
                if (current == '<'){
                    index = str.indexOf('>', index) + 1;
                }
                else{
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length-1){ //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                }else{
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn,200)
            };
            // 延迟1s开始
            setTimeout(timer,1000);
        };
        $("#autotype").autotype();
    },
  },
  mounted () {
    this.play();
    this.startRunTimeCalc();
    this.startWrite();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.App{
  width: 80%;
  max-width: 800px;
  min-width: 400px;
  left: 50%;
  transform: translateX(-50%);
  margin-left: unset;
  margin-right: unset;
}
#autotype{
  margin-top: 40px;
}
</style>



