<template>
  <div id="app">
    <OneMonth />
  </div>
</template>

<script>
// import Envelope from './components/Envelope'
// import Main from './components/Main'
import OneMonth from './components/OneMonth'

export default {
  name: 'App',
  components: {
    OneMonth,
  },
  data () {
    return {
      isShowEmvelope: false,
      iShowMain: false,
    }
  },
  mounted () {
    this.isShowEmvelope = true;
  },
  methods: {
    handleHartClick () {
      this.isShowEmvelope = false;
      this.iShowMain = true;
    }
  },
}
</script>

<style>
*{padding: 0;margin: 0;}
html,body,#app{
  width: 100%;
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
  z-index: 2;
}
canvas{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
</style>



